#sentence {
  margin-top: var(--margin-top);
  font-size: 30pt;
  align-self: flex-start;
  padding: 20px 50px 30px 50px;
  line-height: normal;
  border-radius: 10% / 50%;
  user-select: none;
  -webkit-user-select: none;
  -ms-user-select: none;
  -webkit-touch-callout: none;
  -webkit-text-size-adjust: none;
  touch-action: manipulation; /* blocks double tap zoom */
  background: #f3f3f3;
  transform: translate(calc(50vw - 50%));
  font-family: helvetica;
}
#sentence.completed {
  transition: margin-top 700ms ease-in-out;
  margin-top: 20px;
}
.word {
  display: inline-block;
  position: relative;
  transition: transform 700ms ease-in-out;
}
.correct {
  /* font-style: normal; */
  color: green;
}
.incorrect {
  /* font-style: italic; */
  color: black;
}
.wordSelected {
  text-decoration: underline dotted #00000050;
  -webkit-text-decoration: underline dotted #00000070;
}
.wordSwap {
  transform: translate(var(--transLeft), var(--transTop));
}
.nowrap {
  white-space: nowrap;
}
.punctuation {
}
#attribution {
  font-size: 35px;
  font-family: helvetica;
  margin-top: 15px;
  width: fit-content;
  margin-left: auto;
  margin-right: 0;
  padding-right: 5vh;
  opacity: 0;
  display: none;
}
#attribution.completed {
  transition: opacity 2700ms ease-in-out;
  opacity: 1;
  display: inline-block;
}
